function getSystemInfoObject(){
    var breakpoint = '';
    try {
        breakpoint = responsiveUtils.getCurrentViewport();
    }catch(err){
    }
    var systemObj = {
        os: {
            name: Detectizr.os.name,
            version: Detectizr.os.version,
        },
        // device:{

        // },
        browser: {
            ua: Detectizr.browser.userAgent
        },
        screen: {
            w: window.innerWidth,
            h: window.innerHeight,
            bp: breakpoint
        }
    };
    return systemObj
}